export default {
	data() {
		return {
			source: 0,
			list: [],
			user: {}
		}
	},
	async onLoad(option) {
		let table = uni.getStorageSync("nowTable");
		let res = await this.$api.session(table);
		this.user = res.data;
		res = await this.$api.list('address', {
			userid: this.user.id
		});
		this.list = res.data.list
	},
	async onShow() {
		let table = uni.getStorageSync("nowTable");
		let res = await this.$api.session(table);
		this.user = res.data;
		res = await this.$api.list('address', {
			userid: this.user.id
		});
		this.list = res.data.list
		
	},
	methods: {
		//选择地址
		onSelectTap(item) {
			uni.setStorageSync('address', item);
			uni.navigateBack({
				delta: 1
			})
		},
		onAddressDtailTap(id) {
			let url = ''
			if (id) {
				url = '../shop-address-detail/shop-address-detail?id=' + id
			} else {
				url = '../shop-address-detail/shop-address-detail?id='
			}
			this.$utils.jump(url);
		},
		async onDeleteTap(id) {
			var _this = this;
			let userid = uni.getStorageSync("userid");
			//console.log('-----userid-----',userid);
			uni.showModal({
				title: '提示',
				content: '是否确认删除',
				success: async function(res) {
					if (res.confirm) {
						let res = await _this.$api.del('address', JSON.stringify([id]));
						res = await _this.$api.list('address', {
							userid: userid
						});
						_this.list = res.data.list;
						let address = uni.getStorageSync('address');
						address.address='';
						if(res.data.list.length == 0){ //没有地址清空内容
							uni.setStorageSync("address",address);
							// console.log('----地址清空-----');
							
						}
						_this.$utils.msg('删除成功')
					}
				}
			});
		}
	}
}
